import React from 'react';
import './Screenshots.css';
import ImageZoom from 'react-medium-image-zoom';
// import screenshot from '../../screenshot.png';
import container from '../../screenshots/containerframed.png';
import items from '../../screenshots/itemsframed.png';
import inventory from '../../screenshots/inventoryframed.png';
import commons from '../../screenshots/commonsframed.png';
import item from '../../screenshots/itemframed.png';

const Screenshots = () => {
    return (
      <div className='Screenshots'>
        <ImageZoom
          image={{
            src: inventory,
            alt: 'Screenshot',
            className: 'img',
            style: { padding: '10px' }
          }}
          zoomImage={{
            src: inventory,
            alt: 'Screenshot'
          }}
        />
        
        <ImageZoom
          image={{
            src: item,
            alt: 'Screenshot',
            className: 'img',
            style: { padding: '10px' }
          }}
          zoomImage={{
            src: item,
            alt: 'Screenshot'
          }}
        />
        <ImageZoom
          image={{
            src: container,
            alt: 'Screenshot',
            className: 'img',
            style: { padding: '10px' }
          }}
          zoomImage={{
            src: container,
            alt: 'Screenshot'
          }}
        />
        <ImageZoom
          image={{
            src: items,
            alt: 'Screenshot',
            className: 'img',
            style: { padding: '10px' }
          }}
          zoomImage={{
            src: items,
            alt: 'Screenshot'
          }}
        />
        <ImageZoom
          image={{
            src: commons,
            alt: 'Screenshot',
            className: 'img',
            style: { padding: '10px' }
          }}
          zoomImage={{
            src: commons,
            alt: 'Screenshot'
          }}
        />
      </div>
    );
  }

export default Screenshots;