import React from 'react';
import './Splash.css';
import LeftSide from './LeftSide/LeftSide';
import RightSide from './RightSide/RightSide';

const Splash = () => {
    return (
      <div className='Splash'>
        <div className='Side Left'>
            <LeftSide/>
        </div>
        <div className='Side Right'>
            <RightSide/>
        </div>
      </div>
    );
  }

  export default Splash;