import React from 'react';
import './GettingStartedStep.css';
import ImageZoom from 'react-medium-image-zoom';

class GettingStartedStep extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      photo1: props.photo1,
      photo2: props.photo2,
      text: props.text
    }
  }

  render() {
    return (
      <div className='GettingStartedStep'>
        <div className='GsScreens'>
          <ImageZoom
            image={{
              src: this.state.photo1,
              alt: 'Screenshot',
              className: 'img',
              style: { padding: '10px' }
            }}
            zoomImage={{
              src: this.state.photo1,
              alt: 'Screenshot'
            }}
          />
          <ImageZoom
            image={{
              src: this.state.photo2,
              alt: 'Screenshot',
              className: 'img',
              style: { padding: '10px' }
            }}
            zoomImage={{
              src: this.state.photo2,
              alt: 'Screenshot'
            }}
          />
        </div>
        <div className='GsText'>
          <div>
            <p>
                {this.state.text}
            </p>
          </div>
        </div>
      </div>
    );
  }

}

export default GettingStartedStep;