import React from 'react';
import './GettingStarted.css';
import GettingStartedStep from './GettingStartedStep/GettingStartedStep';
import createinventory from '../../screenshots/createinventoryframed.png';
import inventories from '../../screenshots/inventoriesframed.png';
import createcontainer from '../../screenshots/createcontainerframed.png';
import containers from '../../screenshots/containersframed.png';
import createitem from '../../screenshots/createitemframed.png';
import containerwithitems from '../../screenshots/containerwithitemsframed.png';
import move1 from '../../screenshots/move1framedrepaired.png';
import move2 from '../../screenshots/move2framedrepaired.png';
import items from '../../screenshots/itemsframed.png';
import commons from '../../screenshots/commonsframed.png';

const GettingStarted = () => {
    return (
      <div className='GettingStarted'>
        <GettingStartedStep 
        photo1={createinventory} 
        photo2={inventories} 
        text="First, create your 'inventory', fill in the fields and save it, then select it from the list of inventories"/>
        <GettingStartedStep 
        photo1={createcontainer} 
        photo2={containers} 
        text="Second, create a 'container' in your 'inventory'. Think of a 'container' as a box or a drawer, something that contains other things. Note that the first level of an inventory is made only of containers and not items directly"/>
        <GettingStartedStep 
        photo1={createitem} 
        photo2={containerwithitems} 
        text="Third, create your 'item' inside a 'container', assign a measure and take a picture of it... If you like. You can assign an 'item' to a 'common'. Think of a 'common' as a species of an item, being the item the specimen"/>
        {/* <GettingStartedStep 
        photo1={item} 
        photo2={commons} 
        text='Then, you can watch '/> */}
        <GettingStartedStep 
        photo1={move1} 
        photo2={move2} 
        text="Then, you can MOVE your 'item' to other 'container', just Long Press the item or container you want to move. At last browse to the place you want to move it and press the button at the bottom and select 'Move' on the menu"/>
        <GettingStartedStep 
        photo1={items} 
        photo2={commons} 
        text='You can browse your list of items at anytime, even better if some of them are common to each other'/>
      </div>
    );
}

export default GettingStarted;