import React from 'react';
import './LeftSide.css';
// import screenshot from '../../../screenshot.png';
import container from '../../../screenshots/containerframed.png';
import item from '../../../screenshots/itemframed.png';

const LeftSide = () => {
    return (
      <div className='LeftSide'>
        <img style={{padding: '5px'}} alt='screenshot' src={container}/>
        <img style={{padding: '5px'}} alt='screenshot' src={item}/>
      </div>
    );
  }

export default LeftSide;